<script>
import { Message } from "element-ui";
import { Notification } from "element-ui";

export default {
  data() {
    return {
      activeName: 1,
      currentScroll: 0,
      scrollToOne: true,
      scrollToTwo: true,
    };
  },

  methods: {
    appearInByMark() {
      var target = document.querySelectorAll(".invisible");
      console.log(target);
      if (target) {
        target[0].classList.remove("invisible");
        target[1].classList.remove("invisible");
      }
    },

    tips() {
      Notification.info("暂无本应用开发进度跟踪.");
    },

    addActive(index) {
      this.activeName = index;
    },

    handleChange(val) {
      console.log(val);
    },
    blur_btn(e) {
      e.currentTarget.blur();
    },
    getDownload: function (e) {
      console.log("正在切换至下载页面");
      this.blur_btn(e);
    },
    getMoreDownload(e) {
      console.log("正在切换至更多下载页面");
      this.blur_btn(e);
    },
    getTutorial(e) {
      console.log("正在切换至教程页面");
      this.blur_btn(e);
    },
    windowScroll() {
      // 滚动条距离页面顶部的距离
      let scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      console.log(scrollTop);

      if (this.scrollToOne && scrollTop > 0 && scrollTop < 101) {
        this.currentScroll = this.toLocal("slide_in_container");
        this.appearInByMark();
        this.scrollToOne = false;
        Message.warning(
          "#FIXME:border-bottom:1px 在除火狐外其他浏览器存在白边！"
        );
        return;
      }

      // if (this.scrollToTwo && scrollTop > this.currentScroll) {
      //   this.currentScroll = this.toLocal("slide_in_container_reverse");
      //   this.appearInByMark();
      //   this.scrollToTwo = false;
      //   return;
      // }
    },

    toLocal(location) {
      let parent = document.getElementById(location);
      window.scrollTo({
        top: parent.offsetTop - 70,
        behavior: "smooth",
      });
      return parent.offsetTop - 70;
    },
  },

  mounted() {
    window.addEventListener("scroll", this.windowScroll);
    document.body.parentNode.style.overflowX = "hidden"; //隐藏滚动条
  },

  beforeDestroy() {
    window.removeEventListener("scroll", this.windowScroll);
  },
};
</script>


